import { useState, useEffect } from "react";
import { Button , Text} from 'react-native';

import {
  BrowserRouter as Router,
  Route,
  NavLink,
  Routes,
  Link,
  useParams
} from "react-router-dom";

function ArchersList() {

  let [archers, setArchers] = useState([]);

  useEffect(() => {
    getArchers();
  }, [])

  let getArchers = async () => {
        let response = await fetch('https://archery-score.com/api/archer/');
        let data = await response.json();
        setArchers(data)
  }
  return (
  <div>

            <div>
              {archers.map((archer, index) => (
                 <NavLink to= {"/score/"+archer.id} style={{color: "black",textDecoration:"none"}}>

                    <h2> {archer.full_name} </h2>
                 </NavLink>
              ))}

            </div>

        <NavLink to="/admin123" style={{color: "black",textDecoration:"none"}}>
            <Button title="Menu">

              </Button>
        </NavLink>

    </div>
  );
}

export default ArchersList;
